@brand: #0d6efd;
@muted: #6c757d;

.rounded(@r: .5rem) {
  border-radius: @r;
}

.admin-panel {
  padding: 1rem;
  background: white;
  .rounded(.5rem);
  border: 1px solid fade(@muted, 30%);

  .title {
    color: @brand;
    font-weight: 600;
    margin-bottom: .5rem;
  }

  .actions {
    display: flex;
    gap: .5rem;

    button {
      .rounded(.25rem);
    }
  }
}
